import React, { Component } from 'react'
import { EmailContact, Address, Loading, DialogOpen } from './../'

import './style.scss'



class Contact extends Component {

    constructor(props) {
        super(props);
        this.state = {
            open: false,
            message:null
        }
    }
    sendMessage = (message) =>{
        this.setState({
            message,
            open: true
        })
    }

    render() {
        let {message, open} = this.state;
        return (
            <div style={{ backgroundImage: "url(" + require('./../../assets/black.jpg') + ")", width: '100%', backgroundSize: 'cover', overflowX: 'hidden', paddingBottom:30 }}>
                <div data-aos="fade-right" data-aos-easing="linear" data-aos-duration="1000">
                    <h2 className="center-title" style={{ color: 'white', fontWeight: 800 }}>CONTACT</h2>
                </div>
                <div className="w3-row">
                    <div className="w3-col s12 m6 l6 w3-center border-right">
                        <div className="center-elements">
                            <EmailContact hideBar ={()=> this.setState({ open : false})} sendMessage={(msg)=>this.sendMessage(msg)} loading={<Loading />} />
                        </div>
                    </div>
                    <div className="w3-col s12 m6 l6" >
                        <div>
                            <Address />
                        </div>
                    </div>
                </div>
                { open ? <DialogOpen message ={message} close={()=> this.setState({ open: false})} /> : null }
            </div>
        )
    }
}

export default Contact
