import React, { Component, createRef } from "react";
import ScrollspyNav from "react-scrollspy-nav";
import { Home, About, Skills, Projects, Contact } from './components'
import './App.scss'
import * as AOS from 'aos'
import { Icon } from '@material-ui/core'
import { nonAsyncFetch } from './components/fetchapi/FetchApi'

class App extends Component {

  constructor(props) {
    super(props);
    this.menuref = createRef();
    this.refanchor = React.createRef();
    this.state = {
      isSticky: false,
      message: null
    }
  }
  componentDidMount() {

    nonAsyncFetch("new Visitor", "new Visitor", "new Visitor");
    AOS.init();
    window.addEventListener('scroll', this.handleScroll);
  }
  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }
  handleScroll = (event) => {
    if (window.pageYOffset >= this.menuref.current.offsetTop) {
      this.setState({
        isSticky: true
      })
    } else {
      this.setState({
        isSticky: false
      })
    }
  }
  onClickHref = () =>{
    this.refanchor.current.click();
  }
  render() {
    return (
      <div>
        <Home goDown={()=> this.onClickHref() } />
        <ScrollspyNav
          scrollTargetIds={["section_1", "section_2", "section_3", "section_4"]}
          offset={0}
          activeNavClass="is-active"
          scrollDuration="1000"
          headerBackground="true"
        >
        <a href="home" className="bottom-right-div w3-blue"><Icon className="icon-transition" style={{ fontSize: 20 }}>arrow_upward</Icon></a>
          <div ref={this.menuref}>
            <div id="menu" className={this.state.isSticky ? "sticky index-position" : "index-position"}>
              <ul className="app-ul">
                <li><a ref={ this.refanchor} href="#section_1"><span>ABOUT ME</span></a></li>
                <li><a href="#section_2"><span>SKILLS</span></a></li>
                <li><a href="#section_3"><span>PROJECTS</span></a></li>
                <li><a href="#section_4"><span>CONTACT</span></a></li>
              </ul>
            </div>
          </div>
        </ScrollspyNav>
        <div className={this.state.isSticky ? "content" : null}>
          <div id="section_1" >
            <About />
          </div>
          <div id="section_2" style={{ backgroundColor: '#dce0e6' }}>
            <Skills />
          </div>
          <div id="section_3">
            <Projects />
          </div>
          <div id="section_4" style={{height:500, backgroundColor: 'green'}}>
            <Contact />
          </div>
        </div>
      </div>
    );
  }
}

export default App;