export async function asyncFetch(name, mail, message) {
    let data={ "name": name, "mail": mail, "message": message }
    let res =await fetch('https://mukthahar.herokuapp.com/mail', {
        method: 'POST',
        body: JSON.stringify(data),
        headers: {
            "Content-Type": "application/json"
          },
    });

    return res.ok;
}

export function nonAsyncFetch(name, mail, message) {
    let data={ "name": name, "mail": mail, "message": message }
    let res = fetch('https://mukthahar.herokuapp.com/mail', {
        method: 'POST',
        body: JSON.stringify(data),
        headers: {
            "Content-Type": "application/json"
          },
    });
}