import React, { Component } from 'react'
import './style.scss'
import profileImg from './../../assets/profile.jpeg'


class About extends Component {
    render() {
        return (
            <div>
                <div className="introduction">
                    <div data-aos="fade-right" data-aos-easing="linear" data-aos-duration="1000">
                        <h2 className="center-title">ABOUT ME</h2>
                    </div>
                    <div data-aos="zoom-in" data-aos-easing="linear" data-aos-duration="800">
                        <p>Hi, I am <span className="light-blue">Mukthahar shaik</span>. Professionally I am a software developer. Working in <span className="light-blue">Innoviti Payment solutions</span> in Bangalore, India. As a Front-end Developer, I have 1.5 year of experience. I have passion to learn new technologies.</p>
                    </div>
                    <div data-aos="fade-up" data-aos-easing="linear" data-aos-duration="1000">
                        <img src={profileImg} alt="profile" className="w3-circle profile-img" style={{ width: '250px' }} />
                    </div>
                </div>
            </div>
        )
    }
}

export default About
