import React, { Component } from 'react'
import { Button } from '@material-ui/core';
import './style.scss'
import { asyncFetch } from './../fetchapi/FetchApi'




class EmailContact extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            mail: "",
            message: "",
            nameEmpty: false,
            mailEmpty: false,
            messageEmpty: false,
            loading:false
        }
    }

    async onSubmit() {
        let { name, mail, message } = this.state;
        if (name !== "" && mail !== "" && message !== "") {
            let emailValid = mail.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i);
            if (emailValid) {
                this.setState({
                    loading : true
                })
                this.props.hideBar();
                let res = await asyncFetch(name, mail, message);
                console.log("response : ", res);
                if(res===true){
                    this.setState({
                        loading : false
                    })
                    this.props.sendMessage("Email sent Successfully");
                }
            } else {
                this.setState({ mailEmpty: true })
            }
        } else {
            if (name === "") this.setState({ nameEmpty: true })
            if (mail === "") this.setState({ mailEmpty: true })
            if (message === "") this.setState({ messageEmpty: true })
        }
    }

    render() {
        let { nameEmpty, mailEmpty, messageEmpty, loading } = this.state;
        return (
            <div data-aos="fade-right" data-aos-easing="linear" data-aos-duration="1000">
                <div className="contact-container" style={{ width: '100%' }}>
                    <div>
                        <input onFocus={() => this.setState({ nameEmpty: false })} onKeyUp={(event) => this.setState({
                            name: event.target.value
                        })} className={nameEmpty ? "w3-input error-border-color" : "w3-input"} type="text" placeholder="Name" />
                    </div>
                    <div>
                        <input onFocus={() => this.setState({ mailEmpty: false })} onKeyUp={(event) => this.setState({
                            mail: event.target.value
                        })} className={mailEmpty ? "w3-input error-border-color" : "w3-input"} type="text" placeholder="Mail" />
                    </div>
                    <div>
                        <textarea onFocus={() => this.setState({ messageEmpty: false })} onKeyUp={(event) => this.setState({
                            message: event.target.value
                        })} className={messageEmpty ? "w3-input error-border-color" : "w3-input"} placeholder="Message" style={{ resize: 'none', height: 120 }} />
                    </div>
                    <div className="send-btn-container">
                        <Button disableRipple className= { loading ? "send-btn-disable" : "send-btn"} disableFocusRipple={loading}  onClick={() => this.onSubmit()} style={{ color: 'white', textTransform: "lowercase" }}>
                            { loading ? this.props.loading : "Send"}
                        </Button>
                    </div>
                </div>
            </div>
        )
    }
}

export default EmailContact
