import React, { Component } from 'react'
import './style.scss'
import { Button, Icon } from '@material-ui/core'


class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {
            width: 0,
            height: 0
        }
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    }
    componentDidMount() {
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }

    render() {
        let { width, height } = this.state;
        return (
            <div style={{ backgroundImage: "url(" + require('./../../assets/black.jpg') + ")", height, width, backgroundSize: 'cover' }}>
                <div className="w3-display-middle">
                    <p className="intro">Hello, I'm <span style={{ color: '#DB7093' }}>Mukthahar Shaik</span> Thank you for Visiting my profile</p>
                    <div className="margin-down">
                    <Button onClick={()=> this.props.goDown()} disableRipple className="view-profile-btn" style={{ color: 'white', textTransform: "lowercase" }}>View My Profile <Icon className="icon-transition" style={{ marginLeft: '10px', fontSize: 20 }}>chevron_right</Icon></Button>
                    </div>
                </div>
            </div>
        )
    }
}

export default Home
